.container {
  display: flex;
  flex-flow: column;
  align-items: center;
  position: relative;
  z-index: 1;
  padding-left: 2rem;
  padding-right: 2rem;

  @include breakpoint($bp-mobile) {
    padding-left: 0;
    padding-right: 0;
  }
}

// Homepage Styles
// ===========================
.home {
  color: white;

  a {
    text-decoration: none;
  }
  
  &.page {
    margin-bottom: 0;
  }

  &.admin-bar {

    .site-main {
      height: calc(100vh - 185px);
    }
  }
  .site-main {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 153px);
    text-align: center;
  }

  h2 {
    font-size: cre(28px);
    font-weight: 400;
    letter-spacing: 0.5px;
    line-height: 1.3;
    text-transform: uppercase;
  
    span {
      display: block;
    }
  
    @include breakpoint($bp-mobile) {
      font-size: cre(48px);
      line-height: 1;
  
      span {
        display: inline;
      }
    }
  }
  
  .lead-text {
    display: inline-block;
    font-size: cre(16px);
    font-weight: 300;
    letter-spacing: 1px;
    line-height: 1.618;
    padding-bottom: 1rem;
    position: relative;
    margin-bottom: cre(60px);
  
    @include breakpoint($bp-mobile) {
      font-size: cre(28px);
    }
  
    &::after {
      content: '';
      background-color: rgba(255,255,255,0.3);
      width: 90%;
      height: 1px;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  
  .cta-button {
    display: inline-block;
    background-color: #0097e6;
    padding: 0.5rem 2rem;
    color: white;
    transition: 0.3s;
    margin-bottom: cre(20px);
    
    &:hover {
      color: white;
      text-decoration: none;
      background-color: darken(#0097e6, 10%)
    }
  }
}